import React from 'react';
import { DynamicModuleLoader } from 'redux-dynamic-modules';
import { HashRouter, Route, Switch } from "react-router-dom";
import { CookiesProvider } from 'react-cookie';
import routes from 'routes';
import loginModule from 'redux/module/loginModule';
import loadable from '@loadable/component';

const defaultReduxModules = [
    loginModule,
];

const App = () => {
    const CustomErrorPage = loadable(() => import("pages/ErrorPage/CustomErrorPage.jsx"));
    return (
        <DynamicModuleLoader modules={defaultReduxModules}>
            <CookiesProvider>
                <HashRouter>
                    <Switch>
                        {routes.map((prop, key) => {
                            return (
                                <Route exact path={prop.path} component={prop.component} key={key} />
                            );
                        })}
                        <Route path="*">
                            <CustomErrorPage />
                        </Route>
                    </Switch>
                </HashRouter>
            </CookiesProvider>
        </DynamicModuleLoader>
    )
}

export default App;